import { useEffect, useState } from "react"
import { CloneElement } from "@rp/react/web/common/composite-components"
import { useLocalStorage } from "@rp/react/web/common/hooks"
import { Backdrop, Typography } from "@rp/react/web/mui/core"
import { WATCHLIST_DATA_TABS_ADD_COLUMNS_BUTTON_ID } from "./WatchlistDataTabsColumnsMenu"
import { WatchlistTutorialTooltip } from "./WatchlistTutorialTooltip"

const WATCHLIST_ADD_COLUMNS_TUTORIAL_VISITED_STORAGE_KEY = "watchlists.visitedAddColumnsTutorial"

type Props = {
  onAddColumn: () => void
}

function WatchlistDataTabsAddColumnsTutorial({ onAddColumn }: Props) {
  const [visitedAddColumnsTutorial, setVisitedAddColumnsTutorial] = useLocalStorage<boolean>(
    WATCHLIST_ADD_COLUMNS_TUTORIAL_VISITED_STORAGE_KEY,
    false,
  )
  const [open, setOpen] = useState<boolean>(false)

  useEffect(() => {
    setTimeout(() => setOpen(!visitedAddColumnsTutorial), 300)
  }, [visitedAddColumnsTutorial])

  return (
    <Backdrop
      sx={{
        color: "#fff",
        zIndex: theme => theme.zIndex.drawer + 1,
        "& a, & button": { cursor: "default" },
      }}
      open={!visitedAddColumnsTutorial}
    >
      <WatchlistTutorialTooltip
        offsetX={-130}
        onDone={() => {
          setVisitedAddColumnsTutorial(true)
          setOpen(false)
          onAddColumn()
        }}
        onClose={() => {
          setVisitedAddColumnsTutorial(true)
          setOpen(false)
        }}
        title="Welcome to Watchlists!"
        description={
          <Typography sx={{ fontSize: 14, color: "text.secondary" }}>
            Click the '+' button to add or reorder columns and customize your view.
          </Typography>
        }
        open={open}
      >
        <CloneElement
          selector={`#${WATCHLIST_DATA_TABS_ADD_COLUMNS_BUTTON_ID}`}
          onClick={() => {
            setVisitedAddColumnsTutorial(true)
            setOpen(false)
            onAddColumn()
          }}
        />
      </WatchlistTutorialTooltip>
    </Backdrop>
  )
}

export { WatchlistDataTabsAddColumnsTutorial, WATCHLIST_ADD_COLUMNS_TUTORIAL_VISITED_STORAGE_KEY }
